// 라이트 모드: 주황색 그라데이션 (#ff6f00, #ff9800)
// 다크 모드: 녹색 그라데이션 (#4caf50, #66bb6a)

.membership-header {
  display: flex;
  flex-direction: column;
  background: var(--bg-primary);
  border-bottom: 1px solid var(--border-color);
  box-shadow: 0 2px 4px var(--shadow);
  width: 100%;
  min-height: 64px;
  transition: all 0.3s ease;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.top-menu-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 64px;
  padding: 0 20px;
  color: white;
  // 기본: 라이트 모드 주황색 그라데이션
  background: linear-gradient(135deg, #ff6f00 0%, #ff9800 100%);

  // 다크 모드: 녹색 그라데이션
  :host-context([data-theme='dark']) & {
    background: linear-gradient(135deg, #4caf50 0%, #66bb6a 100%);
  }

  .header-left {
    display: flex;
    align-items: center;
    gap: 16px;
    flex: 1;
    min-width: 0;
    overflow: hidden;

    .menu-toggle-btn {
      background: none;
      border: none;
      color: white;
      cursor: pointer;
      padding: 8px;
      border-radius: 8px;
      transition: background-color 0.2s;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      justify-content: center;

      &:hover {
        background: rgba(255, 255, 255, 0.15);
      }

      &:active {
        background: rgba(255, 255, 255, 0.25);
      }

      mat-icon {
        font-size: 24px;
        width: 24px;
        height: 24px;
      }
    }

    .membership-logo {
      display: flex;
      align-items: center;
      gap: 10px;
      min-width: 0;
      flex: 1;
      cursor: pointer;
      padding: 8px 12px;
      margin: -8px -12px;
      border-radius: 8px;
      transition: background-color 0.2s ease;

      &:hover {
        background: rgba(255, 255, 255, 0.1);
      }

      &:active {
        background: rgba(255, 255, 255, 0.15);
      }

      .logo-icon {
        font-size: 28px;
        width: 28px;
        height: 28px;
        color: #ffd700;
        flex-shrink: 0;
      }

      h1 {
        margin: 0;
        font-size: 20px;
        font-weight: 600;
        letter-spacing: -0.5px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        min-width: 0;
      }
    }

    .membership-stats {
      display: flex;
      align-items: center;
      gap: 6px;
      background: rgba(255, 255, 255, 0.2);
      padding: 6px 14px;
      border-radius: 20px;
      font-size: 13px;
      flex-shrink: 0;
      white-space: nowrap;
      border: 1px solid rgba(255, 255, 255, 0.3);

      .stats-label {
        opacity: 0.9;
        font-weight: 500;
      }

      .stats-count {
        font-weight: 700;
        color: #ffd700;
        font-size: 15px;
      }
    }
  }

  .header-center {
    flex: 2;
    display: flex;
    justify-content: center;
    max-width: 500px;
    margin: 0 20px;

    .search-container {
      display: flex;
      align-items: center;
      background: rgba(255, 255, 255, 0.15);
      border-radius: 24px;
      padding: 10px 18px;
      width: 100%;
      transition: all 0.3s ease;
      border: 1px solid transparent;

      &:hover,
      &.active {
        background: rgba(255, 255, 255, 0.25);
        border-color: rgba(255, 255, 255, 0.4);
      }

      .search-icon {
        font-size: 20px;
        width: 20px;
        height: 20px;
        margin-right: 10px;
        opacity: 0.9;
      }

      .search-input {
        flex: 1;
        background: none;
        border: none;
        color: white;
        font-size: 14px;
        outline: none;

        &::placeholder {
          color: rgba(255, 255, 255, 0.75);
        }
      }

      .search-btn {
        background: rgba(255, 255, 255, 0.25);
        border: none;
        color: white;
        padding: 6px 16px;
        border-radius: 14px;
        font-size: 13px;
        font-weight: 600;
        cursor: pointer;
        transition: all 0.2s;

        &:hover:not(:disabled) {
          background: rgba(255, 255, 255, 0.35);
          transform: translateY(-1px);
        }

        &:disabled {
          opacity: 0.4;
          cursor: not-allowed;
        }
      }
    }
  }

  .header-right {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 8px;

    .toolbar-actions {
      display: flex;
      align-items: center;
      gap: 8px;

      .toolbar-btn {
        background: rgba(255, 255, 255, 0.15);
        border: none;
        color: white;
        cursor: pointer;
        padding: 10px;
        border-radius: 10px;
        transition: all 0.2s ease;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid rgba(255, 255, 255, 0.2);

        mat-icon {
          font-size: 22px;
          width: 22px;
          height: 22px;
        }

        &:hover:not(:disabled) {
          background: rgba(255, 255, 255, 0.25);
          transform: translateY(-2px);
          box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
        }

        &:active:not(:disabled) {
          transform: translateY(0);
        }

        &:disabled {
          opacity: 0.5;
          cursor: not-allowed;
        }

        &.refresh-btn {
          &:not(:disabled):hover mat-icon {
            animation: spin 0.6s ease-in-out;
          }
        }
      }
    }
  }
}

.bottom-info-bar {
  display: flex;
  align-items: center;
  padding: 10px 24px;
  background: var(--bg-secondary);
  border-top: 1px solid var(--border-color);
  min-height: 40px;

  .info-content {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 13px;
    color: var(--text-secondary);

    .info-icon {
      font-size: 18px;
      width: 18px;
      height: 18px;
      opacity: 0.7;
    }

    .info-text {
      font-weight: 500;
    }
  }
}

// 애니메이션
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

// 반응형
@media (max-width: 1024px) {
  .top-menu-bar {
    .header-center {
      max-width: 400px;
      margin: 0 16px;
    }
  }
}

@media (max-width: 768px) {
  .top-menu-bar {
    padding: 0 12px;

    .header-left {
      gap: 8px;

      .membership-logo {
        h1 {
          font-size: 18px;
        }

        .logo-icon {
          font-size: 24px;
          width: 24px;
          height: 24px;
        }
      }

      .membership-stats {
        padding: 4px 10px;
        font-size: 12px;

        .stats-count {
          font-size: 13px;
        }
      }
    }

    .header-center {
      display: none; // 모바일에서는 검색바 숨김
    }

    .header-right {
      .toolbar-actions {
        gap: 4px;

        .toolbar-btn {
          padding: 8px;

          mat-icon {
            font-size: 20px;
            width: 20px;
            height: 20px;
          }
        }
      }
    }
  }

  .bottom-info-bar {
    padding: 8px 16px;

    .info-content {
      font-size: 12px;
    }
  }
}
